.banner{
    background-image: url("/src/Components//assets//heroimg.png");
    height: 200px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.shopCategory-indexsort{
    display: flex;
    padding: 12px 100px;
    justify-content: space-between;
}

@media screen and (max-width:430px) {
    .shopCategory-indexsort{
       
        padding: 12px 8px;
      
    }
}

.items {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
    align-items: center;
    justify-content: center;
}

.items-title{
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
}