.footer{
    margin-top: 32px;
    background-color: black;
    color: white;
    
    display: flex;
    gap: 24px;
    justify-content: center;
    align-items: center;
    padding: 20px 0;
    flex-direction: column;

    p{
        font-weight: 600;
    }
    div{
        align-items: center;
    display: flex;
    gap: 24px;
    justify-content: center;
    }
}

@media screen and (max-width: 430px) {
    .footer div {
        flex-direction: column;
    }
}