.navbar{
background-color: aliceblue;
display: flex;
padding: 10px 30px;
justify-content: space-between;
align-items: center;
position:fixed;
top: 0;
left: 0;
width: 100%;
z-index: 100;
transition: top 0.1s ease-in-out;


h2{
   width: 200px; 
}

}

ul{
    list-style: none;
    display: flex;
    gap: 24px;
    li{
        cursor: pointer;
        color: rgba(0, 0, 0, 0.474);
        transition: color 0.3s, transform 0.3s ease-in-out;
    }
}

.active {
   color: black;
   font-weight: 600;
}

.navbar-left{
    width: 200px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.cartpart{
    position: relative;

    span{
        position: absolute;
        width: 22px;
        display: flex;
       align-items: center;
       justify-content: center;
        height: 22px;
        left: 15px;
        top: -10px;
        background-color: #4CAF50;
        border-radius: 50%;
    }
}

@media screen and (max-width: 430px) {


.navbar{
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    h2{
        text-align: center;
    }
}

