
.item{
    width: 300px;
    border: 1px solid gray;
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    padding: 10px 10px;
    overflow: hidden;
}
.itemimg{
    max-width: 100%;
    display: block; 
    height: 400px;
    transition:  0.3s, transform 0.3s ease-in-out;
    object-fit: cover;
}

.ii{
    display: flex;
    align-items: center;
    justify-content: center;
}

.item:hover{
    img{
        transform: scale(1.05);
    }
}