
.cartitems{
/* border: 1px solid red; */
padding: 100px 0;

}

.cartitemformat{
    /* border: 1px solid blue; */
    display: flex;
    justify-content: space-between;
    background-color: rgb(206, 232, 255);
margin-top: 50px;
    padding: 30px;
    p{
        width: 200px;
      
    }
}

.totaldiv button{
    width: 400px;
  
}

.newcartitems{
    /* border: 1px solid orange; */
    display: flex;
    justify-content: space-between;
    padding: 30px;
    align-items: center;
   .img{
        width: 200px;
        height: 100px;
        /* border: 1px solid red; */
     
        img{
            width: 50%;
            height: 100%;
            object-fit: cover;
        }
    }
    p{
        width: 200px;
        /* border: 1px solid red; */
        display: flex;
        align-items: center;
    }
    .removeb{
        border: 1px solid black;
        background-color: rgb(206, 69, 69);
        color: white;
        cursor: pointer;
    }
    .removeb:hover{
      border-right:4px solid black ;
      border-bottom:4px solid black ;

    }
    button{
        width: 200px;
        background-color: aliceblue;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;

    }

}

.totaldiv{
    /* border: 1px solid red; */
    width: 416px;
    padding: 8px ;
    display: flex;
    flex-direction: column;
    gap: 14px;
}

.totaldiv div{
   
    /* border: 1px solid blue; */
    display: flex;
    justify-content: space-between;
    
}

