.details{
    display: flex;
    /* border: 1px solid red; */
    padding: 80px 0;
}

.imglist{
    display: flex;
    gap: 10px;
    img{
        width: 80px;
        height: 80px;
        object-fit: cover;
    }
}

.displayimage{
    display: flex;
    padding: 10px 0;
    gap: 10px;
}



.mainimg{
    width: 100%;
    height: 320px;
    object-fit: cover;
    transition:  0.3s, transform 0.3s ease-in-out;
}

.mainimg:hover{
    transform: scale(1.01);
}

.rightpart{
    /* border: 1px solid blue; */
    width: 300px;
    display: flex;
    flex-direction: column;
    gap: 24px;
    padding:  24px;
    h2{
        font-size: 32px;
    }
    button{
        width: auto;
    }
}
.sizes{
    /* border: 1px solid red; */
    justify-content: space-around;
    display: flex;
    align-items: center;
    gap: 10px;
    width: 300px;
}

.si{
    width: 20px;
    height: 20px;
    border: 1px solid gray;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.si.clicked{
    border: 2px solid black;

}

@media screen and (max-width:430px) {
    .details{
        flex-direction: column;
        align-items: center;
    }
    
.rightpart{
    align-items: center;
    button{
        width: 100%;
    }
}



}